.container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: calc(1rem * 2);
  padding: 0 1rem;
  min-height: 0;
  min-width: 0;
  margin: auto;
  width: 100%; }
  @supports not (display: grid) {
    .container {
      display: block; } }
  @media (min-width: 600px) {
    .container {
      width: 600px;
      max-width: 600px; } }
  @media (min-width: 900px) {
    .container {
      width: 900px;
      max-width: 900px; } }
  @media (min-width: 1200px) {
